body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-header {
  background-color: white;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.left-bar{
  padding-top: 50px;
  width: 30%;
  background-color: lightblue;
  height:100vh;
  float: left;

}

.right-pane {
  width: 70%;
  float: right;
  height: 100vh;
  text-align: center;
}


.barButton {
  color: white;
  width: 100%;
  background-color: transparent;
  font-size: 24px;
  padding: 10px;
  border: 1px solid white;
  border-left: 0;
}


.quest{
    color: black;
    font-size: 20px;
    text-align: left;
}
.welcomeMessage{
    font-size: 25px;
    color: pink;
}
.graph-container{
    color: black;
    text-align: left;
}

.graph-title{
    font-size: 24px;

}

.graph{
    font-size: 20px;

}

.graph-smaller{
    font-size: 13px;

}

.graph-edges{
    font-size: 13px;

}



.test-cover{
    color: black;
    font-size: 24px;
    text-align: left;
}
